import React, { Component } from 'react';
import Lists from './components/Lists';
import Navbar from './components/Navbar';
import { fetchLists } from './actions/listActions';
import {  } from './actions/listActions';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
class App extends Component {

  constructor(props){
    super(props);
    this.onFetchLists = this.onFetchLists.bind(this);
    this.state = { hasLoadedOnce: false }
  }

  onFetchLists(){
    this.props.fetchLists('/listr_api/lists/', this.props.token);
  }

  componentWillMount(){

  }

  render() {
    var data;
    if(this.props.isLoading){
      data = <p>Loading Data</p>
    }else if(this.props.error){
      data = <p>{this.props.error}</p>
    }
    if(this.props.token && this.state.hasLoadedOnce === false){
      this.props.fetchLists('/listr_api/lists/', this.props.token);
      this.setState({hasLoadedOnce: true});
    }
    if(this.props.lists){
      data = <Lists data={this.props.lists} />
    }
    return (
      <div>
        <Navbar />
        <div class="container">
          <header class="App-header">
            <h1 class="App-title">My Lists</h1>
          </header>
          <button class='btn btn-primary' onClick={this.onFetchLists}>Get Lists</button>
          {data}
        </div>
      </div>
    );
  }
}

App.proptypes = {
  fetchLists: PropTypes.func.isRequired,
  lists: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
  lists: state.items.data,
  error: state.items.error,
  isLoading: state.items.loading
});

export default connect(mapStateToProps, { fetchLists })(App);



