import { FETCH_LISTS, FETCH_LISTS_SUCCESS } from './types';

export const beginFetchLists = () => dispatch =>{
  return{
    type: FETCH_LISTS
  }
}

export const fetchLists = (url, token) => dispatch =>{
    dispatch(beginFetchLists());
    fetch(url, {
      method: 'GET',
      headers: {
        'content-type': 'application/json',
        'Authorization': 'Token ' + token
      },
    })
    .then(res => res.json())
    .then(data => dispatch({
      type: FETCH_LISTS_SUCCESS,
      payload: data
    }));
}



